@import url(https://fonts.googleapis.com/css?family=Fira+Mono:400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* 
From : Fabrizio Bianchi https://codepen.io/_fbrz/pen/mpiFE
Loader #6 - Jelly Box
*/



#loader {
    /* Uncomment this to make it run! */
    /*
       animation: loader 5s linear infinite; 
    */
    
    position: absolute;
    /*top: calc(50% - 20px);*/
    left: calc(50% - 20px);
  }
  @-webkit-keyframes loader {
    0% { left: -100px; }
    100% { left: 110%; }
  }
  @keyframes loader {
    0% { left: -100px; }
    100% { left: 110%; }
  }
  #box {
    width: 50px;
    height: 50px;
    background: #000fff;
    -webkit-animation: animate .5s linear infinite;
            animation: animate .5s linear infinite;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3px;
  }
  @-webkit-keyframes animate {
    17% { border-bottom-right-radius: 3px; }
    25% { -webkit-transform: translateY(9px) rotate(22.5deg); transform: translateY(9px) rotate(22.5deg); }
    50% {
      -webkit-transform: translateY(18px) scale(1,.9) rotate(45deg) ;
              transform: translateY(18px) scale(1,.9) rotate(45deg) ;
      border-bottom-right-radius: 40px;
    }
    75% { -webkit-transform: translateY(9px) rotate(67.5deg); transform: translateY(9px) rotate(67.5deg); }
    100% { -webkit-transform: translateY(0) rotate(90deg); transform: translateY(0) rotate(90deg); }
  }
  @keyframes animate {
    17% { border-bottom-right-radius: 3px; }
    25% { -webkit-transform: translateY(9px) rotate(22.5deg); transform: translateY(9px) rotate(22.5deg); }
    50% {
      -webkit-transform: translateY(18px) scale(1,.9) rotate(45deg) ;
              transform: translateY(18px) scale(1,.9) rotate(45deg) ;
      border-bottom-right-radius: 40px;
    }
    75% { -webkit-transform: translateY(9px) rotate(67.5deg); transform: translateY(9px) rotate(67.5deg); }
    100% { -webkit-transform: translateY(0) rotate(90deg); transform: translateY(0) rotate(90deg); }
  } 
  #shadow { 
    width: 50px;
    height: 5px;
    background: #000;
    opacity: 0.1;
    position: absolute;
    top: 59px;
    left: 0;
    border-radius: 50%;
    -webkit-animation: shadow .5s linear infinite;
            animation: shadow .5s linear infinite;
  }
  @-webkit-keyframes shadow {
    50% {
      -webkit-transform: scale(1.2,1);
              transform: scale(1.2,1);
    }
  }
  @keyframes shadow {
    50% {
      -webkit-transform: scale(1.2,1);
              transform: scale(1.2,1);
    }
  }
  

 
#notfoundContainer{
    width: 80vw;
    height: 80vh;
    padding-top: 20vh;
      align-items: center;
      justify-content: center;
      margin: 0;
      background: #131313;
      color: #fff;
      font-family: 'Fira Mono', monospace;
}

#notfoundDiv{
    display: flex;

    align-items: center;
    justify-content: center;
}

#notfoundComment{
    font-size: 14px;
}

#notfound{
    font-size: 96px;
    letter-spacing: -7px;
  -webkit-animation: glitch 1s linear infinite;
          animation: glitch 1s linear infinite;
}

@-webkit-keyframes glitch{
  2%,64%{
    -webkit-transform: translate(2px,0) skew(0deg);
            transform: translate(2px,0) skew(0deg);
  }
  4%,60%{
    -webkit-transform: translate(-2px,0) skew(0deg);
            transform: translate(-2px,0) skew(0deg);
  }
  62%{
    -webkit-transform: translate(0,0) skew(5deg);
            transform: translate(0,0) skew(5deg); 
  }
}

@keyframes glitch{
  2%,64%{
    -webkit-transform: translate(2px,0) skew(0deg);
            transform: translate(2px,0) skew(0deg);
  }
  4%,60%{
    -webkit-transform: translate(-2px,0) skew(0deg);
            transform: translate(-2px,0) skew(0deg);
  }
  62%{
    -webkit-transform: translate(0,0) skew(5deg);
            transform: translate(0,0) skew(5deg); 
  }
}

#notfound:before,
#notfound:after{
  content: attr(title);
  position: absolute;
  left: 0;
}

#notfound:before{
  -webkit-animation: glitchTop 1s linear infinite;
          animation: glitchTop 1s linear infinite;
  clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
}

@-webkit-keyframes glitchTop{
  2%,64%{
    -webkit-transform: translate(2px,-2px);
            transform: translate(2px,-2px);
  }
  4%,60%{
    -webkit-transform: translate(-2px,2px);
            transform: translate(-2px,2px);
  }
  62%{
    -webkit-transform: translate(13px,-1px) skew(-13deg);
            transform: translate(13px,-1px) skew(-13deg); 
  }
}

@keyframes glitchTop{
  2%,64%{
    -webkit-transform: translate(2px,-2px);
            transform: translate(2px,-2px);
  }
  4%,60%{
    -webkit-transform: translate(-2px,2px);
            transform: translate(-2px,2px);
  }
  62%{
    -webkit-transform: translate(13px,-1px) skew(-13deg);
            transform: translate(13px,-1px) skew(-13deg); 
  }
}

#notfound:after{
  -webkit-animation: glitchBotom 1.5s linear infinite;
          animation: glitchBotom 1.5s linear infinite;
  clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
  -webkit-clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
}

@-webkit-keyframes glitchBotom{
  2%,64%{
    -webkit-transform: translate(-2px,0);
            transform: translate(-2px,0);
  }
  4%,60%{
    -webkit-transform: translate(-2px,0);
            transform: translate(-2px,0);
  }
  62%{
    -webkit-transform: translate(-22px,5px) skew(21deg);
            transform: translate(-22px,5px) skew(21deg); 
  }
}

@keyframes glitchBotom{
  2%,64%{
    -webkit-transform: translate(-2px,0);
            transform: translate(-2px,0);
  }
  4%,60%{
    -webkit-transform: translate(-2px,0);
            transform: translate(-2px,0);
  }
  62%{
    -webkit-transform: translate(-22px,5px) skew(21deg);
            transform: translate(-22px,5px) skew(21deg); 
  }
}
.app {
    height: 100vh;
    background-color: rgba(0,0,0,.05);
    display: grid;
    grid-template-rows: 1fr 100px 100px 1fr 1fr;
    grid-template-columns: repeat(10, 1fr);
}

.title {
    font-size: 64px;
    color: #ff420e;
    grid-row: 2 / 3;
    grid-column: 3 / 7;
}

.subtitle {
    font-size: 32px;
    color: #f98866;
    grid-row: 3 / 4;
    grid-column: 3 / 7;
}
.soon {
    font-size: 48px;
    color: #89da59;
    justify-self: flex-end;
    align-self: flex-end;
    grid-row: 4 / 5;
    grid-column: 7 / 10;
}

@media only screen and (max-width: 1120px) {
    .soon {
        grid-column: 5 / 10;
    }
}

@media only screen and (max-width: 960px) {
    .app {
        grid-template-columns: 10% 1fr 10%;
    }
    .title, .subtitle, .soon {
        grid-column: 2 / 3;
    }
}

@media only screen and (max-width: 360px) {
    .title {
        font-size: 40px;
    }
    .subtitle {
        font-size: 24px;
    }
    .soon {
        font-size: 32px;
    }
}

